// P-012: TODO
import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Alert,
  Button,
  Card,
  CardContent,
  Grid,
  Chip,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Checkbox,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Stack,
  LinearProgress,
  Tooltip
} from '@mui/material';
import {
  SmartToy,
  Check,
  Close,
  Visibility,
  Edit,
  PlayArrow,
  Assessment,
  Schedule,
  Business,
  Person
} from '@mui/icons-material';
import { useAuth } from '../../hooks/useAuth';
import { mockGmailIntegrationService } from '../../services/mock/gmailIntegration.service';
import { MOCK_AI_TODO_CANDIDATES } from '../../services/mock/data/gmailIntegration.mock';
import { TodoPriority, TodoStatus } from '../../types';

export const TodoExtractionPage: React.FC = () => {
  const { user } = useAuth();
  const isMockMode = process.env.NODE_ENV === 'development';

  // State
  const [todoCandidates, setTodoCandidates] = useState<typeof MOCK_AI_TODO_CANDIDATES>([]);
  const [selectedCandidates, setSelectedCandidates] = useState<string[]>([]);
  const [stats, setStats] = useState<any>(null);
  const [isExtracting, setIsExtracting] = useState(false);
  
  // 
  const [previewDialogOpen, setPreviewDialogOpen] = useState(false);
  const [selectedCandidate, setSelectedCandidate] = useState<any>(null);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [batchApprovalOpen, setBatchApprovalOpen] = useState(false);

  // 
  useEffect(() => {
    fetchTodoCandidates();
    fetchStats();
  }, []);

  // TODO
  const fetchTodoCandidates = async () => {
    try {
      // AI
      setTodoCandidates(MOCK_AI_TODO_CANDIDATES);
    } catch (error) {
      console.error('TODO:', error);
    }
  };

  // 
  const fetchStats = async () => {
    try {
      const response = await mockGmailIntegrationService.getGmailStats();
      if (response.success && response.data) {
        setStats(response.data);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  // AI
  const handleStartExtraction = async () => {
    setIsExtracting(true);
    try {
      // TODO
      await new Promise(resolve => setTimeout(resolve, 3000));
      fetchTodoCandidates();
    } catch (error) {
      console.error('TODO:', error);
    } finally {
      setIsExtracting(false);
    }
  };

  // TODO
  const handleApproveCandidate = async (candidateId: string) => {
    try {
      // TODO
      console.log('TODO:', candidateId);
      setTodoCandidates(prev => prev.filter(c => c.id !== candidateId));
    } catch (error) {
      console.error('TODO:', error);
    }
  };

  // TODO
  const handleRejectCandidate = async (candidateId: string) => {
    try {
      // 
      setTodoCandidates(prev => prev.filter(c => c.id !== candidateId));
    } catch (error) {
      console.error('TODO:', error);
    }
  };

  // 
  const handleBatchApproval = async () => {
    try {
      for (const candidateId of selectedCandidates) {
        await handleApproveCandidate(candidateId);
      }
      setSelectedCandidates([]);
      setBatchApprovalOpen(false);
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const getPriorityDisplayName = (priority: TodoPriority): string => {
    switch (priority) {
      case TodoPriority.HIGH: return '';
      case TodoPriority.MEDIUM: return '';
      case TodoPriority.LOW: return '';
      default: return '';
    }
  };

  // 
  const getPriorityColor = (priority: TodoPriority) => {
    switch (priority) {
      case TodoPriority.HIGH: return 'error';
      case TodoPriority.MEDIUM: return 'warning';
      case TodoPriority.LOW: return 'success';
      default: return 'default';
    }
  };

  // 
  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 0.9) return 'success';
    if (confidence >= 0.7) return 'warning';
    return 'error';
  };

  return (
    <Box>
      {/* @MOCK_UI:  */}
      {isMockMode && (
        <Alert severity="info" sx={{ mb: 3 }}>
          [MOCK] TODO - 
        </Alert>
      )}

      {/*  */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Box display="flex" alignItems="center">
          <SmartToy sx={{ mr: 2, color: 'primary.main' }} />
          <Typography variant="h4" component="h1">
            AI TODO
          </Typography>
        </Box>
        <Box display="flex" gap={2}>
          {selectedCandidates.length > 0 && (
            <Button
              variant="outlined"
              startIcon={<Check />}
              onClick={() => setBatchApprovalOpen(true)}
            >
              {selectedCandidates.length}
            </Button>
          )}
          <Button
            variant="contained"
            startIcon={<PlayArrow />}
            onClick={handleStartExtraction}
            disabled={isExtracting}
          >
            {isExtracting ? 'AI...' : 'AI'}
          </Button>
        </Box>
      </Box>

      {/*  */}
      {isExtracting && (
        <Paper sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            AI TODO...
          </Typography>
          <LinearProgress sx={{ mb: 2 }} />
          <Typography variant="body2" color="textSecondary">
            TODO
          </Typography>
        </Paper>
      )}

      {/*  */}
      {stats && (
        <Grid container spacing={3} sx={{ mb: 3 }}>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom variant="body2">
                  
                </Typography>
                <Typography variant="h6">{stats.messagesProcessedToday}</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom variant="body2">
                  TODO
                </Typography>
                <Typography variant="h6">{todoCandidates.length}</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom variant="body2">
                  
                </Typography>
                <Typography variant="h6">
                  {todoCandidates.filter(t => t.confidence >= 0.9).length}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom variant="body2">
                  
                </Typography>
                <Typography variant="h6">{stats.averageResponseTime}</Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      )}

      {/* TODO */}
      <Paper>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    indeterminate={selectedCandidates.length > 0 && selectedCandidates.length < todoCandidates.length}
                    checked={todoCandidates.length > 0 && selectedCandidates.length === todoCandidates.length}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setSelectedCandidates(todoCandidates.map(c => c.id));
                      } else {
                        setSelectedCandidates([]);
                      }
                    }}
                  />
                </TableCell>
                <TableCell>TODO</TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {todoCandidates.map((candidate) => (
                <TableRow key={candidate.id} hover>
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedCandidates.includes(candidate.id)}
                      onChange={(e) => {
                        if (e.target.checked) {
                          setSelectedCandidates([...selectedCandidates, candidate.id]);
                        } else {
                          setSelectedCandidates(selectedCandidates.filter(id => id !== candidate.id));
                        }
                      }}
                    />
                  </TableCell>
                  <TableCell>
                    <Box>
                      <Typography variant="body2" fontWeight="medium">
                        {candidate.text}
                      </Typography>
                      <Typography variant="caption" color="textSecondary">
                        : {candidate.priority} | : {(candidate.confidence * 100).toFixed(0)}%
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Chip 
                      size="small" 
                      label={getPriorityDisplayName(candidate.priority as any)}
                      color={getPriorityColor(candidate.priority as any) as any}
                    />
                  </TableCell>
                  <TableCell>
                    {(candidate as any).dueDate ? (
                      <Box>
                        <Typography variant="body2">
                          {new Date((candidate as any).dueDate).toLocaleDateString('ja-JP')}
                        </Typography>
                        <Typography variant="caption" color="textSecondary">
                          {new Date((candidate as any).dueDate).toLocaleTimeString('ja-JP', { 
                            hour: '2-digit', 
                            minute: '2-digit' 
                          })}
                        </Typography>
                      </Box>
                    ) : (
                      <Typography variant="body2" color="textSecondary">
                        
                      </Typography>
                    )}
                  </TableCell>
                  <TableCell>
                    <Stack direction="row" spacing={0.5}>
                      {(candidate as any).companyId && (
                        <Chip size="small" icon={<Business />} label="" variant="outlined" />
                      )}
                      {(candidate as any).isInternal && (
                        <Chip size="small" label="" color="info" variant="outlined" />
                      )}
                    </Stack>
                  </TableCell>
                  <TableCell>
                    <Chip 
                      size="small" 
                      label={`${Math.round(candidate.confidence * 100)}%`}
                      color={getConfidenceColor(candidate.confidence) as any}
                    />
                  </TableCell>
                  <TableCell>
                    <Stack direction="row" spacing={1}>
                      <Tooltip title="">
                        <IconButton 
                          size="small"
                          onClick={() => {
                            setSelectedCandidate(candidate);
                            setPreviewDialogOpen(true);
                          }}
                        >
                          <Visibility />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="">
                        <IconButton 
                          size="small"
                          color="success"
                          onClick={() => handleApproveCandidate(candidate.id)}
                        >
                          <Check />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="">
                        <IconButton 
                          size="small"
                          color="error"
                          onClick={() => handleRejectCandidate(candidate.id)}
                        >
                          <Close />
                        </IconButton>
                      </Tooltip>
                    </Stack>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      {/* TODO */}
      <Dialog open={previewDialogOpen} onClose={() => setPreviewDialogOpen(false)} maxWidth="md" fullWidth>
        <DialogTitle>TODO</DialogTitle>
        <DialogContent>
          {selectedCandidate && (
            <Box>
              <Typography variant="h6" gutterBottom>
                {selectedCandidate.title}
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{getPriorityDisplayName(selectedCandidate.priority)}</Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{Math.round(selectedCandidate.confidence * 100)}%</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{selectedCandidate.description}</Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">
                    {selectedCandidate.dueDate 
                      ? new Date(selectedCandidate.dueDate).toLocaleString('ja-JP')
                      : ''
                    }
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{selectedCandidate.estimatedHours}</Typography>
                </Grid>
                {selectedCandidate.tags && selectedCandidate.tags.length > 0 && (
                  <Grid item xs={12}>
                    <Typography variant="body2" color="textSecondary"></Typography>
                    <Box display="flex" gap={1} mt={1}>
                      {selectedCandidate.tags.map((tag: string) => (
                        <Chip key={tag} size="small" label={tag} variant="outlined" />
                      ))}
                    </Box>
                  </Grid>
                )}
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setPreviewDialogOpen(false)}></Button>
          <Button 
            color="error" 
            onClick={() => {
              if (selectedCandidate) {
                handleRejectCandidate(selectedCandidate.id);
                setPreviewDialogOpen(false);
              }
            }}
          >
            
          </Button>
          <Button 
            variant="contained" 
            onClick={() => {
              if (selectedCandidate) {
                handleApproveCandidate(selectedCandidate.id);
                setPreviewDialogOpen(false);
              }
            }}
          >
            TODO
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog open={batchApprovalOpen} onClose={() => setBatchApprovalOpen(false)}>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Typography variant="body1">
            {selectedCandidates.length}TODOTODO
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setBatchApprovalOpen(false)}>
            
          </Button>
          <Button variant="contained" onClick={handleBatchApproval}>
            {selectedCandidates.length}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};