// P-002: 
import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Button,
  Alert,
  Chip,
  TextField,
  MenuItem,
  IconButton,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControl,
  InputLabel,
  Select,
  Grid,
  Card,
  CardContent,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TablePagination,
  Avatar,
  Menu,
  ListItemIcon,
  ListItemText,
  Checkbox,
  Tabs,
  Tab,
  Divider
} from '@mui/material';
import {
  Business,
  Add,
  Search,
  MoreVert,
  Edit,
  Delete,
  Visibility,
  Download,
  Refresh,
  Person,
  Assessment,
  TrendingUp
} from '@mui/icons-material';
import { useAuth } from '../../hooks/useAuth';
import { companyService } from '../../services';
import { 
  Company, 
  CompanyStatus,
  CompanySize,
  CompanySearchRequest,
  Contact
} from '../../types';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`company-tabpanel-${index}`}
      aria-labelledby={`company-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

export const CompanyManagementPage: React.FC = () => {
  const isMockMode = process.env.NODE_ENV === 'development';

  // State
  const [companies, setCompanies] = useState<Company[]>([]);
  const [stats, setStats] = useState<any>(null);
  const [selectedCompanies, setSelectedCompanies] = useState<string[]>([]);
  const [tabValue, setTabValue] = useState(0);
  
  // 
  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState<CompanyStatus[]>([]);
  const [sizeFilter, setSizeFilter] = useState<CompanySize[]>([]);
  
  // 
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [totalCount, setTotalCount] = useState(0);
  
  // 
  const [createDialogOpen, setCreateDialogOpen] = useState(false);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [viewDialogOpen, setViewDialogOpen] = useState(false);
  const [selectedCompany, setSelectedCompany] = useState<Company | null>(null);
  const [companyContacts, setCompanyContacts] = useState<Contact[]>([]);
  
  // 
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [menuCompanyId, setMenuCompanyId] = useState<string | null>(null);

  // 
  const fetchCompanies = async () => {
    try {
      const searchRequest: CompanySearchRequest = {
        query: searchQuery || undefined,
        status: statusFilter.length > 0 ? statusFilter : undefined,
        size: sizeFilter.length > 0 ? sizeFilter : undefined,
        page: page + 1,
        limit: rowsPerPage,
        sortBy: 'name',
        sortOrder: 'asc'
      };

      const response = await companyService.searchCompanies(searchRequest);
      
      if (response.success && response.data) {
        setCompanies(response.data.companies);
        setTotalCount(response.data.totalCount);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const fetchStats = async () => {
    try {
      const response = await companyService.getCompanyStats();
      if (response.success && response.data) {
        setStats(response.data);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  useEffect(() => {
    fetchCompanies();
    fetchStats();
  }, [page, rowsPerPage, searchQuery, statusFilter, sizeFilter]);

  // 
  const getStatusDisplayName = (status: CompanyStatus): string => {
    switch (status) {
      case CompanyStatus.LEAD: return '';
      case CompanyStatus.PROSPECT: return '';
      case CompanyStatus.ACTIVE_CUSTOMER: return '';
      case CompanyStatus.INACTIVE: return '';
      default: return '';
    }
  };

  // 
  const getStatusColor = (status: CompanyStatus) => {
    switch (status) {
      case CompanyStatus.LEAD: return 'info';
      case CompanyStatus.PROSPECT: return 'warning';
      case CompanyStatus.ACTIVE_CUSTOMER: return 'success';
      case CompanyStatus.INACTIVE: return 'default';
      default: return 'default';
    }
  };

  // 
  const getSizeDisplayName = (size: CompanySize): string => {
    switch (size) {
      case CompanySize.LARGE: return '';
      case CompanySize.MEDIUM: return '';
      case CompanySize.SMALL: return '';
      default: return '';
    }
  };

  // 
  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, companyId: string) => {
    setAnchorEl(event.currentTarget);
    setMenuCompanyId(companyId);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    setMenuCompanyId(null);
  };

  // 
  const handleViewCompany = async (company: Company) => {
    setSelectedCompany(company);
    
    // 
    try {
      const response = await companyService.getCompanyContacts(company.id);
      if (response.success && response.data) {
        setCompanyContacts(response.data);
      }
    } catch (error) {
      console.error(':', error);
    }
    
    setViewDialogOpen(true);
    handleMenuClose();
  };

  // 
  const handleEditCompany = (company: Company) => {
    setSelectedCompany(company);
    setEditDialogOpen(true);
    handleMenuClose();
  };

  // 
  const StatCard: React.FC<{ title: string; value: number; icon: React.ReactNode; color?: string }> = 
    ({ title, value, icon, color = 'primary' }) => (
    <Card>
      <CardContent>
        <Box display="flex" alignItems="center" justifyContent="space-between">
          <Box>
            <Typography color="textSecondary" gutterBottom variant="body2">
              {title}
            </Typography>
            <Typography variant="h4" component="div">
              {value}
            </Typography>
          </Box>
          <Box color={`${color}.main`} sx={{ display: 'flex' }}>
            {icon}
          </Box>
        </Box>
      </CardContent>
    </Card>
  );

  return (
    <Box>
      {/* @MOCK_UI:  */}
      {isMockMode && (
        <Alert severity="info" sx={{ mb: 3 }}>
          [MOCK]  - 
        </Alert>
      )}

      {/*  */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Box display="flex" alignItems="center">
          <Business sx={{ mr: 2, color: 'primary.main' }} />
          <Typography variant="h4" component="h1">
            
          </Typography>
        </Box>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => setCreateDialogOpen(true)}
        >
          
        </Button>
      </Box>

      {/*  */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs value={tabValue} onChange={(_, newValue) => setTabValue(newValue)}>
          <Tab 
            icon={<Business />} 
            label="" 
            iconPosition="start"
          />
          <Tab 
            icon={<Assessment />} 
            label="" 
            iconPosition="start"
          />
        </Tabs>
      </Box>

      {/*  */}
      <TabPanel value={tabValue} index={0}>
        {/*  */}
        {stats && (
          <Grid container spacing={3} sx={{ mb: 3 }}>
            <Grid item xs={12} sm={6} md={3}>
              <StatCard 
                title="" 
                value={stats.totalCompanies} 
                icon={<Business />} 
                color="primary"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <StatCard 
                title="" 
                value={stats.activeCustomers} 
                icon={<TrendingUp />} 
                color="success"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <StatCard 
                title="" 
                value={stats.prospects} 
                icon={<Assessment />} 
                color="warning"
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <StatCard 
                title="" 
                value={stats.leads} 
                icon={<Add />} 
                color="info"
              />
            </Grid>
          </Grid>
        )}

        {/*  */}
        <Paper sx={{ p: 3, mb: 3 }}>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} md={4}>
              <TextField
                fullWidth
                placeholder="..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                InputProps={{
                  startAdornment: <Search sx={{ mr: 1, color: 'action.active' }} />
                }}
              />
            </Grid>
            <Grid item xs={12} md={2}>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  multiple
                  value={statusFilter}
                  onChange={(e) => setStatusFilter(e.target.value as CompanyStatus[])}
                  label=""
                >
                  {Object.values(CompanyStatus).map(status => (
                    <MenuItem key={status} value={status}>
                      {getStatusDisplayName(status)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} md={2}>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  multiple
                  value={sizeFilter}
                  onChange={(e) => setSizeFilter(e.target.value as CompanySize[])}
                  label=""
                >
                  {Object.values(CompanySize).map(size => (
                    <MenuItem key={size} value={size}>
                      {getSizeDisplayName(size)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} md={2}>
              <Box display="flex" gap={1}>
                <Tooltip title="">
                  <IconButton onClick={fetchCompanies}>
                    <Refresh />
                  </IconButton>
                </Tooltip>
                <Tooltip title="">
                  <IconButton>
                    <Download />
                  </IconButton>
                </Tooltip>
              </Box>
            </Grid>
          </Grid>
        </Paper>

        {/*  */}
        <Paper>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell padding="checkbox">
                    <Checkbox
                      indeterminate={selectedCompanies.length > 0 && selectedCompanies.length < companies.length}
                      checked={companies.length > 0 && selectedCompanies.length === companies.length}
                      onChange={(e) => {
                        if (e.target.checked) {
                          setSelectedCompanies(companies.map(c => c.id));
                        } else {
                          setSelectedCompanies([]);
                        }
                      }}
                    />
                  </TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {companies.map((company) => (
                  <TableRow key={company.id} hover>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={selectedCompanies.includes(company.id)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setSelectedCompanies([...selectedCompanies, company.id]);
                          } else {
                            setSelectedCompanies(selectedCompanies.filter(id => id !== company.id));
                          }
                        }}
                      />
                    </TableCell>
                    <TableCell>
                      <Box display="flex" alignItems="center">
                        <Avatar sx={{ mr: 2, bgcolor: 'primary.main' }}>
                          {company.name.charAt(0)}
                        </Avatar>
                        <Box>
                          <Typography variant="body2" fontWeight="medium">
                            {company.name}
                          </Typography>
                          <Typography variant="caption" color="textSecondary">
                            {company.nameKana}
                          </Typography>
                        </Box>
                      </Box>
                    </TableCell>
                    <TableCell>{company.industry}</TableCell>
                    <TableCell>
                      <Chip 
                        size="small" 
                        label={getSizeDisplayName(company.size)}
                        color="default"
                      />
                    </TableCell>
                    <TableCell>
                      <Chip 
                        size="small" 
                        label={getStatusDisplayName(company.status)}
                        color={getStatusColor(company.status) as any}
                      />
                    </TableCell>
                    <TableCell>
                      <Typography variant="body2">
                        {/* TODO:  */}
                        
                      </Typography>
                    </TableCell>
                    <TableCell>
                      {company.lastContactAt 
                        ? new Date(company.lastContactAt).toLocaleDateString('ja-JP')
                        : ''
                      }
                    </TableCell>
                    <TableCell>
                      <IconButton
                        onClick={(e) => handleMenuOpen(e, company.id)}
                      >
                        <MoreVert />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          
          <TablePagination
            component="div"
            count={totalCount}
            page={page}
            onPageChange={(_, newPage) => setPage(newPage)}
            rowsPerPage={rowsPerPage}
            onRowsPerPageChange={(e) => {
              setRowsPerPage(parseInt(e.target.value, 10));
              setPage(0);
            }}
            labelRowsPerPage=""
            labelDisplayedRows={({ from, to, count }) => `${from}-${to} / ${count}`}
          />
        </Paper>
      </TabPanel>

      {/*  */}
      <TabPanel value={tabValue} index={1}>
        <Typography variant="h6" gutterBottom>
          
        </Typography>
        <Typography variant="body1" color="textSecondary">
          Phase
        </Typography>
      </TabPanel>

      {/*  */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={() => {
          const company = companies.find(c => c.id === menuCompanyId);
          if (company) handleViewCompany(company);
        }}>
          <ListItemIcon><Visibility /></ListItemIcon>
          <ListItemText></ListItemText>
        </MenuItem>
        <MenuItem onClick={() => {
          const company = companies.find(c => c.id === menuCompanyId);
          if (company) handleEditCompany(company);
        }}>
          <ListItemIcon><Edit /></ListItemIcon>
          <ListItemText></ListItemText>
        </MenuItem>
        <Divider />
        <MenuItem onClick={handleMenuClose} sx={{ color: 'error.main' }}>
          <ListItemIcon sx={{ color: 'error.main' }}><Delete /></ListItemIcon>
          <ListItemText></ListItemText>
        </MenuItem>
      </Menu>

      {/*  */}
      <Dialog open={viewDialogOpen} onClose={() => setViewDialogOpen(false)} maxWidth="md" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          {selectedCompany && (
            <Box>
              <Typography variant="h6" gutterBottom>
                {selectedCompany.name}
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{selectedCompany.industry}</Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{getSizeDisplayName(selectedCompany.size)}</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="body2" color="textSecondary"></Typography>
                  <Typography variant="body1">{selectedCompany.description || ''}</Typography>
                </Grid>
              </Grid>
              
              {companyContacts.length > 0 && (
                <Box mt={3}>
                  <Typography variant="h6" gutterBottom></Typography>
                  {companyContacts.map(contact => (
                    <Box key={contact.id} display="flex" alignItems="center" py={1}>
                      <Person sx={{ mr: 2 }} />
                      <Box>
                        <Typography variant="body2">{contact.fullName} ({contact.position})</Typography>
                        <Typography variant="caption" color="textSecondary">{contact.email}</Typography>
                      </Box>
                    </Box>
                  ))}
                </Box>
              )}
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setViewDialogOpen(false)}></Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog open={createDialogOpen} onClose={() => setCreateDialogOpen(false)} maxWidth="md" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Typography variant="body2" color="textSecondary">
            Phase
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setCreateDialogOpen(false)}></Button>
          <Button variant="contained"></Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog open={editDialogOpen} onClose={() => setEditDialogOpen(false)} maxWidth="md" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Typography variant="body2" color="textSecondary">
            Phase
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setEditDialogOpen(false)}></Button>
          <Button variant="contained"></Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};