// P-011: Gmail
import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Alert,
  Button,
  Card,
  CardContent,
  Switch,
  FormControlLabel,
  TextField,
  Grid,
  Chip,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  LinearProgress,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction
} from '@mui/material';
import {
  Settings,
  Mail,
  Sync,
  CheckCircle,
  Error,
  Refresh,
  LinkOff,
  Schedule,
  Security
} from '@mui/icons-material';
import { useAuth } from '../../hooks/useAuth';
import { gmailService } from '../../services';
import { GmailSyncStatus } from '../../types';

export const GmailSettingsPage: React.FC = () => {
  const { user } = useAuth();
  const isMockMode = process.env.NODE_ENV === 'development';

  // State
  const [syncStatus, setSyncStatus] = useState<GmailSyncStatus>(GmailSyncStatus.DISCONNECTED);
  const [lastSyncAt, setLastSyncAt] = useState<Date | null>(null);
  const [stats, setStats] = useState<any>(null);
  const [isConnecting, setIsConnecting] = useState(false);
  const [isSyncing, setIsSyncing] = useState(false);
  
  // 
  const [autoSync, setAutoSync] = useState(true);
  const [syncInterval, setSyncInterval] = useState(5);
  const [notificationEnabled, setNotificationEnabled] = useState(true);
  const [todoExtraction, setTodoExtraction] = useState(true);
  
  // 
  const [connectDialogOpen, setConnectDialogOpen] = useState(false);
  const [disconnectDialogOpen, setDisconnectDialogOpen] = useState(false);

  // 
  useEffect(() => {
    fetchSyncStatus();
    fetchStats();
  }, []);

  // 
  const fetchSyncStatus = async () => {
    try {
      const response = await gmailService.getSyncStatus();
      if (response.success && response.data) {
        setSyncStatus(response.data);
        setLastSyncAt(new Date());
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const fetchStats = async () => {
    try {
      // 
      // const response = await gmailService.getGmailStats();
      // if (response.success && response.data) {
      //   setStats(response.data);
      // }
    } catch (error) {
      console.error(':', error);
    }
  };

  // Gmail
  const handleConnect = async () => {
    setIsConnecting(true);
    try {
      // Gmail OAuth
      if (!user?.id) return;
      const authUrlResponse = await gmailService.getConnectUrl(user.id);
      if (authUrlResponse.success && authUrlResponse.data) {
        // OAuth
        window.location.href = authUrlResponse.data.authUrl;
        return;
      }
      const response = { success: false, error: 'Gmail' };
      if (response.success) {
        setSyncStatus(GmailSyncStatus.CONNECTED);
        fetchSyncStatus();
        fetchStats();
      }
    } catch (error) {
      console.error('Gmail:', error);
    } finally {
      setIsConnecting(false);
      setConnectDialogOpen(false);
    }
  };

  // Gmail
  const handleDisconnect = async () => {
    try {
      if (!user?.id) return;
      const response = await gmailService.disconnectGmail(user.id);
      if (response.success) {
        setSyncStatus(GmailSyncStatus.DISCONNECTED);
        setLastSyncAt(null);
        setStats(null);
      }
    } catch (error) {
      console.error('Gmail:', error);
    } finally {
      setDisconnectDialogOpen(false);
    }
  };

  // 
  const handleManualSync = async () => {
    setIsSyncing(true);
    try {
      const response = await gmailService.syncGmail();
      if (response.success) {
        fetchSyncStatus();
        fetchStats();
      }
    } catch (error) {
      console.error(':', error);
    } finally {
      setIsSyncing(false);
    }
  };

  // 
  const getStatusDisplay = (status: GmailSyncStatus) => {
    switch (status) {
      case GmailSyncStatus.CONNECTED:
        return { label: '', color: 'success', icon: <CheckCircle /> };
      case GmailSyncStatus.SYNCING:
        return { label: '', color: 'info', icon: <Sync /> };
      case GmailSyncStatus.ERROR:
        return { label: '', color: 'error', icon: <Error /> };
      default:
        return { label: '', color: 'default', icon: <Mail /> };
    }
  };

  const statusDisplay = getStatusDisplay(syncStatus);

  return (
    <Box>
      {/* @MOCK_UI:  */}
      {isMockMode && (
        <Alert severity="info" sx={{ mb: 3 }}>
          [MOCK] Gmail - 
        </Alert>
      )}

      {/*  */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Box display="flex" alignItems="center">
          <Settings sx={{ mr: 2, color: 'primary.main' }} />
          <Typography variant="h4" component="h1">
            Gmail
          </Typography>
        </Box>
      </Box>

      <Grid container spacing={3}>
        {/*  */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Gmail
              </Typography>
              <Box display="flex" alignItems="center" mb={2}>
                {statusDisplay.icon}
                <Box ml={2}>
                  <Chip 
                    label={statusDisplay.label}
                    color={statusDisplay.color as any}
                    sx={{ mb: 1 }}
                  />
                  {lastSyncAt && (
                    <Typography variant="caption" color="textSecondary" display="block">
                      : {lastSyncAt.toLocaleString('ja-JP')}
                    </Typography>
                  )}
                </Box>
              </Box>
              
              <Box display="flex" gap={1}>
                {syncStatus === GmailSyncStatus.CONNECTED ? (
                  <>
                    <Button
                      variant="outlined"
                      startIcon={<Refresh />}
                      onClick={handleManualSync}
                      disabled={isSyncing}
                    >
                      {isSyncing ? '...' : ''}
                    </Button>
                    <Button
                      variant="outlined"
                      color="error"
                      startIcon={<LinkOff />}
                      onClick={() => setDisconnectDialogOpen(true)}
                    >
                      
                    </Button>
                  </>
                ) : (
                  <Button
                    variant="contained"
                    startIcon={<Mail />}
                    onClick={() => setConnectDialogOpen(true)}
                    disabled={isConnecting}
                  >
                    {isConnecting ? '...' : 'Gmail'}
                  </Button>
                )}
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/*  */}
        {stats && (
          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  
                </Typography>
                <Grid container spacing={2}>
                  <Grid item xs={6}>
                    <Typography variant="body2" color="textSecondary"></Typography>
                    <Typography variant="h6">{stats.totalThreads}</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <Typography variant="body2" color="textSecondary"></Typography>
                    <Typography variant="h6">{stats.unreadThreads}</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <Typography variant="body2" color="textSecondary">AI</Typography>
                    <Typography variant="h6">{stats.aiProcessedThreads}</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <Typography variant="body2" color="textSecondary">TODO</Typography>
                    <Typography variant="h6">{stats.todoExtractedCount}</Typography>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        )}

        {/*  */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                <Schedule sx={{ mr: 1, verticalAlign: 'middle' }} />
                
              </Typography>
              <Box>
                <FormControlLabel
                  control={
                    <Switch
                      checked={autoSync}
                      onChange={(e) => setAutoSync(e.target.checked)}
                      disabled={syncStatus !== GmailSyncStatus.CONNECTED}
                    />
                  }
                  label=""
                />
                <Box mt={2}>
                  <TextField
                    label=""
                    type="number"
                    value={syncInterval}
                    onChange={(e) => setSyncInterval(Number(e.target.value))}
                    disabled={!autoSync || syncStatus !== GmailSyncStatus.CONNECTED}
                    size="small"
                    inputProps={{ min: 1, max: 60 }}
                  />
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* AI */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                <Security sx={{ mr: 1, verticalAlign: 'middle' }} />
                AI
              </Typography>
              <Box>
                <FormControlLabel
                  control={
                    <Switch
                      checked={todoExtraction}
                      onChange={(e) => setTodoExtraction(e.target.checked)}
                      disabled={syncStatus !== GmailSyncStatus.CONNECTED}
                    />
                  }
                  label="TODO"
                />
                <FormControlLabel
                  control={
                    <Switch
                      checked={notificationEnabled}
                      onChange={(e) => setNotificationEnabled(e.target.checked)}
                      disabled={syncStatus !== GmailSyncStatus.CONNECTED}
                    />
                  }
                  label=""
                />
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/*  */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Typography variant="body2" color="textSecondary" mb={2}>
                Gmail
              </Typography>
              
              <List>
                <ListItem>
                  <ListItemText
                    primary=""
                    secondary=", "
                  />
                  <ListItemSecondaryAction>
                    <Button size="small"></Button>
                  </ListItemSecondaryAction>
                </ListItem>
                <Divider />
                <ListItem>
                  <ListItemText
                    primary=""
                    secondary=", "
                  />
                  <ListItemSecondaryAction>
                    <Button size="small"></Button>
                  </ListItemSecondaryAction>
                </ListItem>
                <Divider />
                <ListItem>
                  <ListItemText
                    primary=""
                    secondary="90"
                  />
                  <ListItemSecondaryAction>
                    <Button size="small"></Button>
                  </ListItemSecondaryAction>
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Gmail */}
      <Dialog open={connectDialogOpen} onClose={() => setConnectDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Gmail</DialogTitle>
        <DialogContent>
          <Typography variant="body1" mb={2}>
            Gmail
          </Typography>
          <Typography variant="body2" color="textSecondary">
             Gmail
          </Typography>
          {isConnecting && (
            <Box mt={2}>
              <LinearProgress />
              <Typography variant="caption" color="textSecondary" mt={1}>
                Gmail...
              </Typography>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setConnectDialogOpen(false)} disabled={isConnecting}>
            
          </Button>
          <Button variant="contained" onClick={handleConnect} disabled={isConnecting}>
            {isConnecting ? '...' : 'Gmail'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Gmail */}
      <Dialog open={disconnectDialogOpen} onClose={() => setDisconnectDialogOpen(false)}>
        <DialogTitle>Gmail</DialogTitle>
        <DialogContent>
          <Typography variant="body1">
            Gmail
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDisconnectDialogOpen(false)}>
            
          </Button>
          <Button color="error" variant="contained" onClick={handleDisconnect}>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};