/**
 * 
 * 
 */

import React, { createContext, useContext, useReducer, useEffect, ReactNode } from 'react';
import { User, LoginRequest, RefreshTokenRequest } from '../types/index';
import { authService } from '../services/index';

// 
interface AuthState {
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  error: string | null;
  accessToken: string | null;
  refreshToken: string | null;
}

// 
type AuthAction =
  | { type: 'AUTH_LOADING' }
  | { type: 'AUTH_SUCCESS'; payload: { user: User; accessToken: string; refreshToken: string } }
  | { type: 'AUTH_ERROR'; payload: string }
  | { type: 'AUTH_LOGOUT' }
  | { type: 'AUTH_CLEAR_ERROR' }
  | { type: 'AUTH_TOKEN_REFRESH'; payload: { accessToken: string; refreshToken: string } };

// 
interface AuthContextType extends AuthState {
  login: (credentials: LoginRequest) => Promise<void>;
  googleLogin: () => Promise<void>;
  logout: () => Promise<void>;
  refreshTokens: () => Promise<void>;
  clearError: () => void;
  checkAuthStatus: () => Promise<void>;
}

// 
const initialState: AuthState = {
  user: null,
  isAuthenticated: false,
  isLoading: false,
  error: null,
  accessToken: null,
  refreshToken: null
};

// 
function authReducer(state: AuthState, action: AuthAction): AuthState {
  switch (action.type) {
    case 'AUTH_LOADING':
      return {
        ...state,
        isLoading: true,
        error: null
      };

    case 'AUTH_SUCCESS':
      return {
        ...state,
        user: action.payload.user,
        accessToken: action.payload.accessToken,
        refreshToken: action.payload.refreshToken,
        isAuthenticated: true,
        isLoading: false,
        error: null
      };

    case 'AUTH_ERROR':
      return {
        ...state,
        user: null,
        accessToken: null,
        refreshToken: null,
        isAuthenticated: false,
        isLoading: false,
        error: action.payload
      };

    case 'AUTH_LOGOUT':
      return {
        ...initialState
      };

    case 'AUTH_CLEAR_ERROR':
      return {
        ...state,
        error: null
      };

    case 'AUTH_TOKEN_REFRESH':
      return {
        ...state,
        accessToken: action.payload.accessToken,
        refreshToken: action.payload.refreshToken,
        error: null
      };

    default:
      return state;
  }
}

// 
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// 
interface AuthProviderProps {
  children: ReactNode;
}

export function AuthProvider({ children }: AuthProviderProps) {
  const [state, dispatch] = useReducer(authReducer, initialState);

  /**
   * 
   */
  const login = async (credentials: LoginRequest): Promise<void> => {
    dispatch({ type: 'AUTH_LOADING' });
    
    try {
      // @MOCK_TO_API: authService.login()/API
      const response = await authService.login(credentials);
      
      // 
      localStorage.setItem('accessToken', response.accessToken);
      localStorage.setItem('refreshToken', response.refreshToken);
      
      dispatch({
        type: 'AUTH_SUCCESS',
        payload: {
          user: response.user,
          accessToken: response.accessToken,
          refreshToken: response.refreshToken
        }
      });

      console.log(' Login Success - User:', response.user.email);
      
    } catch (error: any) {
      const errorMessage = error.message || '';
      dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
      console.error(' Login Failed:', errorMessage);
      throw error;
    }
  };

  /**
   * Google OAuth2
   */
  const googleLogin = async (): Promise<void> => {
    dispatch({ type: 'AUTH_LOADING' });
    
    try {
      // @MOCK_TO_API: authService.googleLogin()/API
      const authUrl = await authService.googleLogin();
      
      // Google
      window.location.href = authUrl;
      
    } catch (error: any) {
      const errorMessage = error.message || 'Google';
      dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
      console.error(' Google Login Failed:', errorMessage);
      throw error;
    }
  };

  /**
   * 
   */
  const logout = async (): Promise<void> => {
    try {
      // @MOCK_TO_API: authService.logout()/API
      await authService.logout(state.refreshToken || undefined);
      
      // 
      localStorage.removeItem('accessToken');
      localStorage.removeItem('refreshToken');
      
      dispatch({ type: 'AUTH_LOGOUT' });
      
      console.log(' Logout Success');
      
    } catch (error: any) {
      console.error(' Logout Error:', error.message);
      // 
      dispatch({ type: 'AUTH_LOGOUT' });
      localStorage.removeItem('accessToken');
      localStorage.removeItem('refreshToken');
    }
  };

  /**
   * 
   */
  const refreshTokens = async (): Promise<void> => {
    const currentRefreshToken = state.refreshToken || localStorage.getItem('refreshToken');
    
    if (!currentRefreshToken) {
      dispatch({ type: 'AUTH_ERROR', payload: '' });
      return;
    }

    try {
      // @MOCK_TO_API: authService.refreshToken()/API
      const response = await authService.refreshToken({ refreshToken: currentRefreshToken });
      
      // 
      localStorage.setItem('accessToken', response.accessToken);
      localStorage.setItem('refreshToken', response.refreshToken);
      
      dispatch({
        type: 'AUTH_TOKEN_REFRESH',
        payload: {
          accessToken: response.accessToken,
          refreshToken: response.refreshToken
        }
      });

      console.log(' Token Refresh Success');
      
    } catch (error: any) {
      const errorMessage = error.message || '';
      dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
      console.error(' Token Refresh Failed:', errorMessage);
      
      // 
      localStorage.removeItem('accessToken');
      localStorage.removeItem('refreshToken');
    }
  };

  /**
   * 
   */
  const clearError = (): void => {
    dispatch({ type: 'AUTH_CLEAR_ERROR' });
  };

  /**
   * 
   */
  const checkAuthStatus = async (): Promise<void> => {
    const token = localStorage.getItem('accessToken');
    const refreshTokenStored = localStorage.getItem('refreshToken');
    
    if (!token || !refreshTokenStored) {
      return;
    }

    dispatch({ type: 'AUTH_LOADING' });

    try {
      // @MOCK_TO_API: authService.getCurrentUser()/API
      const user = await authService.getCurrentUser();
      
      if (user) {
        dispatch({
          type: 'AUTH_SUCCESS',
          payload: {
            user,
            accessToken: token,
            refreshToken: refreshTokenStored
          }
        });
        console.log(' Auth Status Check - User Found:', user.email);
      }
      
    } catch (error: any) {
      console.log(' Auth Status Check Failed - Token may be expired');
      // 
      await refreshTokens();
    }
  };

  // 
  useEffect(() => {
    checkAuthStatus();
  }, []);

  // 5
  useEffect(() => {
    if (!state.isAuthenticated || !state.accessToken) return;

    // 12153
    const refreshInterval = setInterval(() => {
      refreshTokens();
    }, 12 * 60 * 1000);

    return () => clearInterval(refreshInterval);
  }, [state.isAuthenticated, state.accessToken]);

  const contextValue: AuthContextType = {
    ...state,
    login,
    googleLogin,
    logout,
    refreshTokens,
    clearError,
    checkAuthStatus
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
}

/**
 * 
 */
export function useAuth(): AuthContextType {
  const context = useContext(AuthContext);
  
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  
  return context;
}

/**
 * 
 */
export function useRequireAuth(): AuthContextType {
  const auth = useAuth();
  
  useEffect(() => {
    if (!auth.isLoading && !auth.isAuthenticated) {
      console.warn(' Authentication required - redirecting to login');
      // 
      // window.location.href = '/login';
    }
  }, [auth.isAuthenticated, auth.isLoading]);
  
  return auth;
}