// P-001: 
// HTMLlogin.htmlReact
import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  TextField,
  Typography,
  Container,
  Paper,
  Alert,
  Divider,
  CircularProgress
} from '@mui/material';
import { Google as GoogleIcon } from '@mui/icons-material';
import { LoginRequest, LoginResponse } from '../../types';
import { 
  mockLoginSuccessResponse, 
  mockAdminLoginResponse, 
  mockUserLoginResponse,
  mockGoogleAuthResponse,
  getMockUserByCredentials 
} from '../../services/mock/data/auth.mock';

interface LoginPageProps {}

const LoginPage: React.FC<LoginPageProps> = () => {
  const [formData, setFormData] = useState<LoginRequest>({
    email: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [mfaRequired, setMfaRequired] = useState(false);
  const [mfaCode, setMfaCode] = useState('');
  const [loginAttempts, setLoginAttempts] = useState(0);

  // @MOCK_TO_API: API
  const handleTraditionalLogin = async () => {
    if (!formData.email || !formData.password) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);
    setLoginAttempts(prev => prev + 1);

    try {
      // @MOCK_TO_API: API
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const mockUser = getMockUserByCredentials(formData.email, formData.password);
      
      if (!mockUser) {
        throw new Error('');
      }

      // @MOCK_LOGIC: 
      if (loginAttempts === 0) {
        setMfaRequired(true);
        setLoading(false);
        return;
      }

      // @MOCK_DATA: 
      let loginResponse: LoginResponse;
      if (formData.email.includes('admin') || formData.email.includes('leader')) {
        loginResponse = mockAdminLoginResponse;
      } else {
        loginResponse = mockUserLoginResponse;
      }

      console.log(' MOCK Login Success:', loginResponse);
      alert('');
      
    } catch (err) {
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  // @MOCK_TO_API: Google OAuthAPI
  const handleGoogleLogin = async () => {
    setLoading(true);
    setError(null);

    try {
      // @MOCK_TO_API: Google OAuth 
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // @MOCK_DATA: Google
      const loginResponse = mockGoogleAuthResponse;

      console.log(' MOCK Google Login Success:', loginResponse);
      alert('Google');
      
    } catch (err) {
      setError('Google');
    } finally {
      setLoading(false);
    }
  };

  // @MOCK_TO_API: MFAAPI
  const handleMfaVerification = async () => {
    if (!mfaCode || mfaCode.length !== 6) {
      setError('6');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      // @MOCK_TO_API: MFAAPI
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // @MOCK_DATA: MFA
      if (mfaCode === '123456') {
        console.log(' MOCK MFA Success');
        alert('');
        setMfaRequired(false);
      } else {
        throw new Error('');
      }
      
    } catch (err) {
      setError(err instanceof Error ? err.message : 'MFA');
    } finally {
      setLoading(false);
    }
  };

  // @MOCK_TO_API: API
  const handleForgotPassword = () => {
    const email = prompt(':');
    if (email) {
      alert(`${email} `);
    }
  };

  // @MOCK_TO_API: 
  const handleRegister = () => {
    alert('');
  };

  // 
  useEffect(() => {
    const handleKeyPress = (e: KeyboardEvent) => {
      if (e.key === 'Enter') {
        if (mfaRequired) {
          handleMfaVerification();
        } else {
          handleTraditionalLogin();
        }
      }
    };

    document.addEventListener('keypress', handleKeyPress);
    return () => document.removeEventListener('keypress', handleKeyPress);
  }, [formData, mfaCode, mfaRequired]);

  return (
    <Box
      sx={{
        minHeight: '100vh',
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: 2
      }}
    >
      {/* @MOCK_UI:  */}
      <Alert 
        severity="warning" 
        sx={{ 
          position: 'fixed', 
          top: 0, 
          left: 0, 
          right: 0, 
          zIndex: 1000,
          borderRadius: 0
        }}
      >
          - 
      </Alert>

      <Container maxWidth="sm">
        <Paper
          elevation={8}
          sx={{
            padding: 6,
            borderRadius: 4,
            textAlign: 'center',
            maxWidth: 400,
            margin: '0 auto'
          }}
        >
          {/*  */}
          <Box sx={{ marginBottom: 4 }}>
            <Typography 
              variant="h4" 
              component="h1" 
              color="primary" 
              fontWeight={500}
              gutterBottom
            >
              
            </Typography>
            <Typography variant="body2" color="text.secondary">
              
            </Typography>
          </Box>

          {error && (
            <Alert severity="error" sx={{ marginBottom: 2 }}>
              {error}
            </Alert>
          )}

          {!mfaRequired ? (
            <>
              {/* @MOCK_UI:  */}
              <Alert severity="info" sx={{ marginBottom: 3 }}>
                <Typography variant="body2" gutterBottom>
                  <strong>:</strong>
                </Typography>
                <Box sx={{ marginTop: 1 }}>
                  <Button 
                    size="small"
                    onClick={() => {
                      setFormData({ email: 'user@company.com', password: 'password' });
                      setTimeout(() => handleTraditionalLogin(), 100);
                    }}
                    sx={{ marginRight: 1 }}
                  >
                    
                  </Button>
                  <Button 
                    size="small"
                    onClick={() => {
                      setFormData({ email: 'leader@company.com', password: 'password' });
                      setTimeout(() => handleTraditionalLogin(), 100);
                    }}
                  >
                    
                  </Button>
                </Box>
              </Alert>

              {/* Google */}
              <Button
                fullWidth
                variant="contained"
                startIcon={<GoogleIcon />}
                onClick={handleGoogleLogin}
                disabled={loading}
                sx={{
                  backgroundColor: '#4285f4',
                  '&:hover': { backgroundColor: '#357ae8' },
                  marginBottom: 3,
                  padding: 1.5
                }}
              >
                {loading ? <CircularProgress size={24} color="inherit" /> : 'Google'}
              </Button>

              {/*  */}
              <Divider sx={{ marginY: 3 }}>
                <Typography variant="body2" color="text.secondary">
                  
                </Typography>
              </Divider>

              {/*  */}
              <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                <TextField
                  fullWidth
                  type="email"
                  label=""
                  placeholder=": user@company.com"
                  value={formData.email}
                  onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
                  disabled={loading}
                  required
                />
                
                <TextField
                  fullWidth
                  type="password"
                  label=""
                  placeholder=""
                  value={formData.password}
                  onChange={(e) => setFormData(prev => ({ ...prev, password: e.target.value }))}
                  disabled={loading}
                  required
                />
                
                <Button
                  fullWidth
                  variant="contained"
                  onClick={handleTraditionalLogin}
                  disabled={loading}
                  sx={{ padding: 1.5, marginTop: 1 }}
                >
                  {loading ? <CircularProgress size={24} color="inherit" /> : ''}
                </Button>
              </Box>
            </>
          ) : (
            /* MFA */
            <Box sx={{ textAlign: 'center' }}>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                6
              </Typography>
              <Typography variant="body2" color="info.main" sx={{ marginBottom: 2 }}>
                : 123456
              </Typography>
              
              <TextField
                fullWidth
                label=""
                placeholder="000000"
                value={mfaCode}
                onChange={(e) => setMfaCode(e.target.value)}
                inputProps={{ maxLength: 6, style: { textAlign: 'center', letterSpacing: '4px' } }}
                disabled={loading}
                sx={{ marginBottom: 2 }}
              />
              
              <Button
                fullWidth
                variant="contained"
                onClick={handleMfaVerification}
                disabled={loading}
                sx={{ padding: 1.5 }}
              >
                {loading ? <CircularProgress size={24} color="inherit" /> : ''}
              </Button>
            </Box>
          )}

          {/*  */}
          <Box sx={{ marginTop: 3, display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', gap: 1 }}>
            <Button variant="text" size="small" onClick={handleForgotPassword}>
              
            </Button>
            <Button variant="text" size="small" onClick={handleRegister}>
              
            </Button>
          </Box>

          {/*  */}
          <Alert severity="info" sx={{ marginTop: 3, textAlign: 'left' }}>
            <Typography variant="body2">
              <strong> :</strong> 
              
            </Typography>
          </Alert>
        </Paper>
      </Container>
    </Box>
  );
};

export default LoginPage;